* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list{
      list-style: none;
      display: flex;
      flex-direction: column;
}

 .country-list__item {
  display: flex;
  align-items: center;
  }

 .country-list__img {
   height: 30px;
   margin-right: 10px;
 }

 .country-list__text {
    font-size: 18px;
    font-weight: 500;
 }

 .country-info{
    display: flex;
    flex-direction: column;
 }
.country-info_list{
  list-style: none;
}

 .country-info__img {
   height: 50px;
   margin-right: 20px;
 }

.country-info__item {
  display: flex;
  align-items: center;
}

.country-info__title {
  margin: 0;
}

.country-info__text {
  margin-right: 10px;
}